@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: url(/static/media/home.657bfcef.jpeg);
  background-position: center;
  font-family: 'Comfortaa', cursive;
  color: white;
}

.App{
  height: 100vh;
  overflow-y: scroll;
}
.navigation{position:fixed;top:0;width:100%;z-index:9}.nav-bg{background-image:linear-gradient(to right, #2e2d2d, #35352d)}#menu{transition:.2s}.link:focus{outline:none}.link:active{outline:none}.left-100{left:100%}.left-0{left:0}

.particles{position:fixed;top:0;bottom:0;right:0;left:0;z-index:-1}header{height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;line-height:7vh}header h1{font-size:10vw;font-family:'Pacifico', cursive;color:white;text-shadow:1px 1px 1px rgba(0,0,0,0.4)}header h2{font-family:'Comfortaa', cursive;font-feature-settings:normal;font-variant-caps:normal;font-size:3vw;padding:1em;background-color:black;color:yellow;z-index:-1}

h1{font-size:1em}h2{font-size:.8em}.grow:hover{z-index:2;transition:.3s}.grow{z-index:0;transition:.3s}

.bradius{border-radius:25px}.w75{width:30vw}

.main{background-image:linear-gradient(to right, #2e2d2d, #35352d);min-height:100vh;padding-top:6vh;color:white;text-align:center}

